import CardList from "../../components/Cards/CardList3.vue";
export default {
  computed: {
    cardList: function cardList() {
      return this.$store.getters["administratorStore/mainMenuList"];
    }
  },
  components: {
    CardList: CardList
  },
  methods: {
    showPage: function showPage(payload) {
      this.$router.push(payload.to);
    },
    setBackRoute: function setBackRoute() {
      var user = this.$store.getters["authenticationStore/IdentityModel"];
      var backRoute = "";
      if (user.schoolId === "CEO") backRoute = '/super-admin';else backRoute = '/admin';
      this.$store.commit("authenticationStore/setBackRoute", backRoute);
    }
  },
  created: function created() {
    var context = this;
    this.$store.commit("authenticationStore/setIsError", false);
    this.$store.commit("authenticationStore/setErrorMessages", "");
    context.setBackRoute();
  }
};